import GameManager from './managers/GameManager';

// Games
import GameSnake from './games/snake/Game';
import GameArkanoid from './games/arkanoid/Game';
import Game2048 from './games/2048/Game';

const gameSnake = new GameSnake();
const gameArkanoid = new GameArkanoid();
const game2048 = new Game2048();

const games = [
  {
    name: 'snake',
    game: gameSnake,
    helper: [
      { key: 'arrow right &rarr;', description: ' ' },
      { key: 'arrow left &larr;', description: ' ' },
      { key: 'arrow up &uarr;', description: ' ' },
      { key: 'arrow down &darr;', description: ' ' },
      { key: 'esc', description: '' }
    ]
  },
  {
    name: '2048',
    game: game2048,
    helper: [
      { key: 'arrow right &rarr;', description: ' ' },
      { key: 'arrow left &larr;', description: ' ' },
      { key: 'arrow up &uarr;', description: ' ' },
      { key: 'arrow down &darr;', description: ' ' },
      { key: 'esc', description: '' }
    ]
  },
  {
    name: 'arkanoid',
    game: gameArkanoid,
    helper: [
      { key: 'arrow right &rarr;', description: ' ' },
      { key: 'arrow left &larr;', description: ' ' },
      { key: 'space', description: ' ' },
      { key: 'esc', description: '' }
    ]
  },
];

window.addEventListener('load', () => {
  GameManager.init(games);
});
